import React, { useState, useEffect } from 'react';
import axios from 'axios';
import Cookies from 'js-cookie';

const App = () => {
    const [file, setFile] = useState(null);
    const [subjectSelections, setSubjectSelections] = useState([]);
    const [subjectTeacherPairs, setSubjectTeacherPairs] = useState([]);
    const [timetables, setTimetables] = useState([]);
    const [selectedTimetable, setSelectedTimetable] = useState(0);
    const [error, setError] = useState('');
    const years = ['1st Year', '2nd Year', '3rd Year', '4th Year'];

    useEffect(() => {
        // Fetch subject-teacher pairs on mount
        axios.get('/')
            .then(response => {
                if (response.data.subject_teacher_pairs) {
                    setSubjectTeacherPairs(response.data.subject_teacher_pairs);
                }
            })
            .catch(err => console.error('Error fetching pairs:', err));
    }, []);

    const handleFileChange = (e) => {
        setFile(e.target.files[0]);
    };

    const addSubjectSelection = () => {
        setSubjectSelections([...subjectSelections, { subjectTeacher: '', hours: '', year: '1st Year' }]);
    };

    const updateSubjectSelection = (index, field, value) => {
        const newSelections = [...subjectSelections];
        newSelections[index][field] = value;
        setSubjectSelections(newSelections);
    };

    const removeSubjectSelection = (index) => {
        setSubjectSelections(subjectSelections.filter((_, i) => i !== index));
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        setError('');
        setTimetables([]);
        setSelectedTimetable(0);

        if (!file) {
            setError('Please upload the constraints file');
            return;
        }

        const formData = new FormData();
        formData.append('file', file);

        subjectSelections.forEach((selection, index) => {
            if (selection.subjectTeacher && selection.hours && selection.year) {
                formData.append(`subject_teacher_${index + 1}`, selection.subjectTeacher);
                formData.append(`hours_${index + 1}`, selection.hours);
                formData.append(`year_${index + 1}`, selection.year);
            }
        });

        try {
            const response = await axios.post('/', formData, {
                headers: {
                    'X-CSRFToken': Cookies.get('csrftoken'),
                    'Content-Type': 'multipart/form-data'
                }
            });

            if (response.data.timetables) {
                setTimetables(response.data.timetables);
                setSubjectTeacherPairs(response.data.subject_teacher_pairs || subjectTeacherPairs);
            } else {
                setError('No timetables generated');
            }
        } catch (err) {
            setError(err.response?.data?.error || 'Error generating timetables');
        }
    };

    const renderTimetable = (timetable) => {
        return (
            <div>
                {years.map(year => (
                    <div key={year} className="mb-4">
                        <h3 className="text-lg font-bold">{year}</h3>
                        {timetable[year]?.length > 0 ? (
                            <table className="w-full border-collapse border">
                                <thead>
                                    <tr>
                                        <th className="border p-2">Subject</th>
                                        <th className="border p-2">Teacher</th>
                                        <th className="border p-2">Day</th>
                                        <th className="border p-2">Time</th>
                                        <th className="border p-2">Venue</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {timetable[year].map((slot, idx) => (
                                        <tr key={idx}>
                                            <td className="border p-2">{slot.subject}</td>
                                            <td className="border p-2">{slot.teacher}</td>
                                            <td className="border p-2">{slot.day}</td>
                                            <td className="border p-2">{slot.start}:00 - {slot.end}:00</td>
                                            <td className="border p-2">{slot.venue}</td>
                                        </tr>
                                    ))}
                                </tbody>
                            </table>
                        ) : (
                            <p>No lectures scheduled for {year}</p>
                        )}
                    </div>
                ))}
            </div>
        );
    };

    return (
        <div className="container mx-auto p-4">
            <h1 className="text-2xl font-bold mb-4">Timetable Generator</h1>
            {error && <p className="text-red-500 mb-4">{error}</p>}
            <form onSubmit={handleSubmit} className="mb-4">
                <div className="mb-4">
                    <label className="block mb-2">Upload Constraints File (Excel)</label>
                    <input
                        type="file"
                        accept=".xlsx"
                        onChange={handleFileChange}
                        className="border p-2 w-full"
                    />
                </div>
                <div className="mb-4">
                    <h2 className="text-xl font-semibold mb-2">Select Subjects and Hours</h2>
                    {subjectSelections.map((selection, index) => (
                        <div key={index} className="flex mb-2 items-center">
                            <select
                                value={selection.subjectTeacher}
                                onChange={(e) => updateSubjectSelection(index, 'subjectTeacher', e.target.value)}
                                className="border p-2 mr-2"
                            >
                                <option value="">Select Subject-Teacher</option>
                                {subjectTeacherPairs.map((pair, idx) => (
                                    <option key={idx} value={`${pair.subject}-${pair.teacher}`}>
                                        {pair.subject} - {pair.teacher}
                                    </option>
                                ))}
                            </select>
                            <input
                                type="number"
                                placeholder="Hours/Week"
                                value={selection.hours}
                                onChange={(e) => updateSubjectSelection(index, 'hours', e.target.value)}
                                className="border p-2 mr-2 w-24"
                                min="1"
                            />
                            <select
                                value={selection.year}
                                onChange={(e) => updateSubjectSelection(index, 'year', e.target.value)}
                                className="border p-2 mr-2"
                            >
                                {years.map(year => (
                                    <option key={year} value={year}>{year}</option>
                                ))}
                            </select>
                            <button
                                type="button"
                                onClick={() => removeSubjectSelection(index)}
                                className="bg-red-500 text-white p-2 rounded"
                            >
                                Remove
                            </button>
                        </div>
                    ))}
                    <button
                        type="button"
                        onClick={addSubjectSelection}
                        className="bg-blue-500 text-white p-2 rounded mt-2"
                    >
                        Add Subject
                    </button>
                </div>
                <button type="submit" className="bg-green-500 text-white p-2 rounded">
                    Generate Timetables
                </button>
            </form>
            {timetables.length > 0 && (
                <div>
                    <h2 className="text-xl font-semibold mb-2">Select Timetable</h2>
                    <select
                        value={selectedTimetable}
                        onChange={(e) => setSelectedTimetable(parseInt(e.target.value))}
                        className="border p-2 mb-4"
                    >
                        {timetables.map((_, idx) => (
                            <option key={idx} value={idx}>Timetable {idx + 1}</option>
                        ))}
                    </select>
                    {renderTimetable(timetables[selectedTimetable])}
                </div>
            )}
        </div>
    );
};

export default App;